import React from "react";
import {ArticleList} from "./ArticleList";
import {useListArticlesQuery} from "../generated/graphql";

interface TopProps {}

export const TopScreen: React.FC<TopProps> = props => {
  const {loading, error, data} = useListArticlesQuery({
    errorPolicy: 'all',
  })

  if (loading) {
    return <span>Loading...</span>
  }

  if (error != null) {
    return <div>Error:
      {error.networkError?.message}
      {error.graphQLErrors.map(({message}, i) =>
        <span key={i}>{message}</span>
      )}
    </div>
  }

  if (data != null && data.articles ) {
    return (
      <div>
        <ArticleList articles={data.articles}/>
      </div>
    )
  }

  return <div>something wrong</div>
};


