import React from "react";
import {Article as Data, Content, ContentType, Text, TextType} from "../generated/graphql";
import {Prism as SyntaxHighlighter} from 'react-syntax-highlighter';
import styled from "styled-components";
import {Theme} from "../theme/Theme";

interface ArticleProps {
  data: Data
}

const Container = styled.div`
  padding: ${Theme.ContentPadding};
`;

const Title = styled.h1`
  font-size: 1.5em;
  padding: 10px 10px;
  border-left: 5px solid hsl(0, 0%, 20%);
`

const PublishedDate = styled.div`
  text-align: right;
  color: hsl(0, 0%, 60%);
`

export const Article: React.FC<ArticleProps> = props => {
  return <Container>
    <Title>{props.data.title}</Title>
    <PublishedDate>{new Date(props.data.publishedAt * 1000).toLocaleDateString()}</PublishedDate>
    {convertContents(props.data.contents)}
  </Container>
};

const convertContents: (contents: Array<Content>) => JSX.Element = contents => {
  const result = new Array<JSX.Element>()
  while (contents.length > 0) {
    const [e, remain] = createElement(contents)
    result.push(<div key={result.length}>{e}</div>)
    contents = remain
  }
  
  return <div>
    {result}
  </div>
}

const Quote = styled.blockquote`
  background: hsl(0, 0%, 95%);
  padding: 10px;
  font-style: italic;
  box-shadow: 1px 1px 5px hsla(0, 0%, 0%, 0.1);
  border-left: 5px solid hsl(0, 0%, 60%);
}
`

const createElement: (contents: Content[]) => [JSX.Element, Content[]] = contents => {
  const head = contents[0]
  
  if (head == null) {
    throw new Error("head is empty")
  }
  
  switch (head.type) {
    case ContentType.Header:
      return [createHeader(head), contents.slice(1)]
    case ContentType.Paragraph:
      return [<p>{createText(head.texts)}</p>, contents.slice(1)]
    case ContentType.CodeBlock:
      return [
        <SyntaxHighlighter language={head.language!} showLineNumbers={true} customStyle={{"font-size": "0.9em"}}>
          {head.texts[0].text.replace(/\n$/g, "")}
        </SyntaxHighlighter>,
        contents.slice(1),
      ]
    case ContentType.List:
      return createList(contents)
    case ContentType.Quote:
      return [<Quote>{createText(head.texts)}</Quote>, contents.slice(1)]
    default:
      return [<div>{createText(head.texts)}</div>, contents.slice(1)]
  }
}

const Code = styled.code`
  background: rgb(245, 242, 240);
  margin: auto 3px; 
  padding: 2px 7px; 
  font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  font-size: 0.8em;
  font-weight: bold;
`

const Link = styled.a`
  color: rgb(150, 150, 250);
  text-decoration: none;
  
  &:visited {
    color: rgb(150, 150, 250);
  }
  
  &:hover {
    color: rgb(250, 180, 180);
    text-decoration: underline;
  }
`

const createText: (texts: Text[]) => JSX.Element[] = texts => {
  return texts.flatMap((txt, idx) => {
    switch (txt.type) {
      case TextType.Text:
        const texts = txt.text.split("\n")
        return texts.map((s, idx2) => {
          if (idx2 === texts.length - 1) {
            return <span key={idx+"-"+idx2}>{s}</span>
          } else {
            return <span key={idx+"-"+idx2}>{s}<br/></span>
          }
        })
      case TextType.Code:
        return <Code key={idx}>{txt.text}</Code>
      case TextType.Link:
        return <Link key={idx} href={txt.url!}>{txt.text}</Link>
      default:
        throw new Error(`unknown text type: ${txt.type}`)
    }
  })
}

const H1 = styled.h2`
  border-left: 5px solid hsl(0, 0%, 40%);
  padding: 3px 10px;
  font-size: 1.4em;
`;

const H2 = styled.h3`
  border-left: 5px solid hsl(0, 0%, 60%);
  padding: 3px 10px;
  font-size: 1.3em;
`;

const H3 = styled.h4`
  border-left: 5px solid hsl(0, 0%, 80%);
  padding: 3px 10px;
  font-size: 1.2em;
`;

const createHeader: (content: Content) => JSX.Element = content => {
  if (content.level == null) {
    throw new Error("empty header level")
  }

  const text = createText(content.texts)
  switch (content.level!) {
    case 1:
      return <H1>{text}</H1>
    case 2:
      return <H2>{text}</H2>
    case 3:
      return <H3>{text}</H3>
    default:
      throw new Error(`unsupported header level: ${content.level!}`)
  }
}

const createList: (contents: Content[]) => [JSX.Element, Content[]] = contents => {
  const notListIdx = contents.findIndex(c => c.type !== ContentType.List)
  const items = contents.slice(0, notListIdx)
  
  const [result] = createUL(1, items)
  
  return [result, contents.slice(notListIdx)]
}

const createUL: (level: number, items: Content[]) => [JSX.Element, Content[]] = (level, items) => {
  const result = new Array<JSX.Element>()
  while (items.length > 0) {
    const item = items[0]
    if (item.level! === level) {
      result.push(<li key={level+"-"+items.length}>{createText(item.texts)}</li>)
      items = items.slice(1)
    } else if (item.level! < level) {
      break
    } else {
      const [ul, remain] = createUL(item.level!, items)
      result.push(ul)
      items = remain
    }
  }
  return [<ul key={level}>{result}</ul>, items]
}
